import React, { Component } from 'react';
import {NavLink} from "react-router-dom";

export default class Footer extends Component {
	render() {
		const year = new Date().getFullYear();

		return (
			<div className="footer">
				<div className="content footer-content">
					<div className="footer-content-left">
						<NavLink to="/" strict exact>
							<span className="logo footer-logo" />
						</NavLink>
						<ul className="menu footer-menu">
							<li className="footer-menu-item"><NavLink to="/"></NavLink></li>
							<li className="footer-menu-item"><NavLink to="/seed"></NavLink></li>
							<li className="footer-menu-item"><NavLink to="/about"> </NavLink></li>
							<li className="footer-menu-item hide"><NavLink to="/information"> </NavLink></li>
							<li className="footer-menu-item"><NavLink to="/team"></NavLink></li>
							<li className="footer-menu-item"><NavLink to="/partners"></NavLink></li>
							<li className="footer-menu-item"><NavLink to="/contacts"></NavLink></li>
						</ul>
					</div>
					<div className="footer-content-right">
						<div className="footer-contacts">
							<div className="footer-contacts-h3">  </div>
							<div className="footer-contacts-address">
								. , 8,
							</div>
							<div className="footer-contacts-address">
								. , 03067, 
							</div>
							<div className="footer-contacts-phone">
								/: <a className="link" href="tel:+38 (044) 290-50-16">+38 (044) 290-50-16</a>
							</div>
							<div className="footer-contacts-mail">
								<a className="link" href="mailto:office@danube-agro.com.ua">office@danube-agro.com.ua</a>
							</div>
						</div>
					</div>
				</div>
				<div className="content footer-bottom">
					<div className="footer-copyright">
						<div className="copyright-top">  -     <NavLink className="link" to="/">Probstdorfer Saatzucht</NavLink>  .</div>
						<div className="copyright-bottom">&copy; {year}       | <NavLink className="link" to="/"> </NavLink></div>
					</div>
					<div className="partners-logo">
						<a href="http://www.probstdorfer.at/" target="_blank">
							<span className="logo footer-logo" />
						</a>
					</div>
				</div>
			</div>
		);
	}
};


