import React, { Component } from 'react';
import SeedSwiper from "./seed_swiper";
import Swiper from 'react-id-swiper';


export default class Home extends Component {

	get slides() {

		const swiper_slide = [
			{ path: "wheat/winter", 	class: "_wheat", 		title: "" },
			{ path: "soy", 				class: "_soy", 			title: "" },
			{ path: "raps", 			class: "_raps", 		title: "" },
			{ path: "barley/spring", 	class: "_barley", 		title: "" },
			{ path: "sunflower", 		class: "_sunflower", 	title: "" }
		];

		return swiper_slide.map(slide =>
			<SeedSwiper {...slide} />
		)
	}

	render() {

		const swiper_setup = {
			slidesPerView: 5,
			breakpoints: {
				1024: {
					slidesPerView: 4
				},
				768: {
					slidesPerView: 3
				},
				640: {
					slidesPerView: 2
				},
				320: {
					slidesPerView: 1
				}
			}
		};

		return (
			<div className="homepage">
				<header className="header">
					<div className="header-background">
						<div className="header-image">
							<div className="header-quote">
								<div className="quote-part"> Wie du sst, so wirst du ernten!</div>
								<div className="quote-part">      !</div>
							</div>
							<a href="http://www.probstdorfer.at/" target="_blank">
								<span className="logo-probs" />
							</a>
						</div>
					</div>
				</header>
				<div className="categories">
					<Swiper {...swiper_setup}>
						{this.slides}
					</Swiper>
				</div>
				<div className="content we-are">
					<div className="columns">
						<div className="left-column">
							<h2 className="title">  </h2>
							<div className="description">         ,      .      ,      ,             ,             .</div>
							<div className="description"><span className="_bold">Probstdorfer Saatzucht</span>     70   ,  , ,    .</div>
						</div>
						<div className="right-column">
							<div className="we-are-image" />
						</div>
						<div className="achievements">
							<div className="achievement">
								<div className="top">30</div>
								<div className="bottom"></div>
							</div>
							<div className="achievement">
								<div className="top">360</div>
								<div className="bottom"></div>
							</div>
							<div className="achievement">
								<div className="top">5</div>
								<div className="bottom"></div>
							</div>
							<div className="achievement">
								<div className="top">35</div>
								<div className="bottom"></div>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
};


